import React, { useEffect, useState, useRef } from "react"; // Added useRef
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";

const RegisteredUsersLayer = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);
  const [name,setName] = useState("");

  // Create a ref for the modal
  const modalRef = useRef(null);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/users`
        );
        setUsers(res.data);
      } catch (error) {
        console.error("Error fetching users:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchUsers();
  }, []);

  // Handle clicks outside the modal
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showModal
      ) {
        setShowModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showModal]);

  const handleDelete = async (id) => {
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this user?"
    );
    if (!confirmDelete) return;

    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${id}`
      );
      setUsers(users.filter((user) => user._id !== id));
      toast.success("User deleted Successfully");
    } catch (error) {
      console.error("Error deleting user:", error);
      toast.error("Failed to delete user. Please try again.");
    }
  };

         useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              showModal
            ) {
              showModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (showModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [showModal]);


const [currentPage, setCurrentPage] = useState(1);
const usersPerPage = 10; // Changed to 10 records per page
const indexOfLastUser = currentPage * usersPerPage;
const indexOfFirstUser = indexOfLastUser - usersPerPage;
const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);
const totalPages = Math.ceil(users.length / usersPerPage);

const handlePageChange = (pageNumber) => {
  setCurrentPage(pageNumber);
};

     const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

const renderPagination = () => {
  const pages = [];
  const maxVisible = 4; // ðŸ‘ˆ only show 4 pages at a time
  let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
  let endPage = startPage + maxVisible - 1;

  if (endPage > totalPages) {
    endPage = totalPages;
    startPage = Math.max(1, endPage - maxVisible + 1);
  }

  pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

  // Prev Button
  pages.push(
    <li key="prev" className={`page-item ${currentPage === 1 ? "disabled" : ""}`}>
      <button
        className="page-link"
        onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
      >
        Prev
      </button>
    </li>
  );

  // Page Numbers
  for (let i = startPage; i <= endPage; i++) {
    pages.push(
      <li key={i} className={`page-item ${currentPage === i ? "active" : ""}`}>
        <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
          {i}
        </button>
      </li>
    );
  }

  // Next Button
  pages.push(
    <li
      key="next"
      className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)}
      >
        Next
      </button>
    </li>
  );

  pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );

  return pages;
};

const fetchSearchedUsers = async (query) => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/searchUsers?query=${query}`
      );
      setUsers(res.data);
    } catch (error) {
      console.error("Error fetching users:", error);
      setUsers([]);
    }
  };

  // 3ï¸âƒ£ Debounced live search effect
  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (name.trim() !== "") {
        fetchSearchedUsers(name); // call search API
      } else {
        // if input cleared â†’ show all users again
        const fetchAllUsers = async () => {
          try {
            const res = await axios.get(
              `${process.env.REACT_APP_BACKEND_URL}/api/users`
            );
            setUsers(res.data);
          } catch (error) {
            console.error("Error fetching users:", error);
          }
        };
        fetchAllUsers();
      }
    }, 500); // wait 0.5s after user stops typing

    return () => clearTimeout(delayDebounce);
  }, [name]);

  return (
   <div className="dashboard-body__content">
  <div className="card common-card border border-gray-five">
    <div className="card-body">
      {/* Header Section */}
      <div className="d-flex flex-column flex-md-row justify-content-between align-items-start align-items-md-center gap-2">
        <h5 className="mb-0">Registered Users</h5>
        <input
          type="text"
          className="common-input border-gray-five"
          value={name}
          
          onChange={(e) => setName(e.target.value)}
          placeholder="Search Users"
          style={{ maxWidth: "350px" }}
        />
      </div>

      {loading ? (
        <p>Loading users...</p>
      ) : users.length > 0 ? (
        <>
          <div className="table-responsive">
            <table className="table text-body">
              <thead>
                <tr>
                  <th>SN.</th>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Mobile</th>
                  <th>Gender</th>
                  <th className="text-center">Action</th>
                </tr>
              </thead>
              <tbody>
                {currentUsers.map((user, i) => (
                  <tr key={user._id}>
                    <td>{indexOfFirstUser + i + 1}</td>
                    <td>
                      {user.firstName} {user.lastName}
                    </td>
                    <td>{user.email}</td>
                    <td>+91 {user.mobileNumber}</td>
                    <td>{user.gender}</td>
                    <td
                      style={{
                        display: "flex",
                        gap: "10px",
                        justifyContent: "center",
                      }}
                    >
                      <button
                        style={{
                          backgroundColor: "green",
                          color: "white",
                          borderRadius: "4px",
                          padding: "5px 10px",
                          gap: "10px",
                        }}
                        onClick={() => {
                          setSelectedUser(user);
                          setShowModal(true);
                        }}
                      >
                        View
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <nav aria-label="Service pagination" className="mt-3">
            <ul className="pagination justify-content-center">
              {renderPagination()}
            </ul>
          </nav>
        </>
      ) : (
        <p>No registered users found.</p>
      )}
    </div>
  </div>

  {/* Modal remains the same */}
  {showModal && selectedUser && (
    <div
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "rgba(0,0,0,0.5)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: 1000,
        overflowY: "auto",
      }}
      onClick={() => setShowModal(false)}
    >
      <div
        ref={modalRef}
        style={{
          backgroundColor: "#fff",
          borderRadius: "10px",
          maxWidth: "800px",
          width: "95%",
          boxShadow: "0 0 10px rgba(0,0,0,0.3)",
          position: "relative",
          overflow: "hidden",
          overflowY: "auto",
          maxHeight: "90vh",
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div
          style={{
            backgroundColor: "#3a3a3a",
            color: "#fff",
            padding: "15px 20px",
            fontSize: "18px",
            fontWeight: "bold",
            position: "sticky",
            top: 0,
            zIndex: 10,
          }}
        >
          User Details
          <button
            onClick={() => setShowModal(false)}
            style={{
              position: "absolute",
              top: "10px",
              right: "10px",
              background: "transparent",
              border: "none",
              fontSize: "22px",
              color: "#fff",
              cursor: "pointer",
            }}
          >
            &times;
          </button>
        </div>

        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            padding: "20px",
            gap: "20px",
            color: "#333",
          }}
        >
          {/* Left Column */}
          <div style={{ flex: 1, minWidth: "250px" }}>
            <div style={{ marginBottom: "25px" }}>
              <strong>Name</strong>
              <p>
                {selectedUser.firstName} {selectedUser.lastName}
              </p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Email</strong>
              <p>{selectedUser.email || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Gender</strong>
              <p>{selectedUser.gender || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Zipcode</strong>
              <p>{selectedUser.zipcode || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>City</strong>
              <p>{selectedUser.city || "N/A"}</p>
            </div>
          </div>

          {/* Right Column */}
          <div style={{ flex: 1, minWidth: "250px" }}>
            <div style={{ marginBottom: "25px" }}>
              <strong>Phone</strong>
              <p>{selectedUser.mobileNumber || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Alt Phone</strong>
              <p>{selectedUser.altMobileNumber || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Age</strong>
              <p>{selectedUser.age || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>Locality</strong>
              <p>{selectedUser.address || "N/A"}</p>
            </div>
            <div style={{ marginBottom: "25px" }}>
              <strong>State</strong>
              <p>{selectedUser.state || "N/A"}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )}
  <ToastContainer position="top-right" autoClose={3000} />
</div>
  );
};

export default RegisteredUsersLayer;
